import React, { useState, useEffect } from 'react';
import { useNavigate, useSearchParams } from 'react-router-dom';
import {
  Container, Box, TextField, Button, Typography, Paper,
  Alert, CircularProgress, IconButton, InputAdornment
} from '@mui/material';
import { Visibility, VisibilityOff, CheckCircle } from '@mui/icons-material';
import axios from 'axios';

const ResetPassword = () => {
  const [searchParams] = useSearchParams();
  const navigate = useNavigate();
  const token = searchParams.get('token');

  const [newPassword, setNewPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [showPassword, setShowPassword] = useState(false);
  const [showConfirmPassword, setShowConfirmPassword] = useState(false);
  const [loading, setLoading] = useState(false);
  const [validating, setValidating] = useState(true);
  const [tokenValid, setTokenValid] = useState(false);
  const [success, setSuccess] = useState(false);
  const [error, setError] = useState('');
  const API_BASE_URL = "https://thirushipickles-backend-1.onrender.com";
  useEffect(() => {
    validateToken();
  }, [token]);

  const validateToken = async () => {
    if (!token) {
      setError('Invalid reset link');
      setValidating(false);
      return;
    }

    try {
      const response = await axios.get(
        `${API_BASE_URL}/api/auth/reset-password/validate?token=${token}`
      );
      
      if (response.data.valid) {
        setTokenValid(true);
      } else {
        setError('This reset link is invalid or has expired');
      }
    } catch (err) {
      setError('Failed to validate reset link');
    } finally {
      setValidating(false);
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    if (newPassword.length < 6) {
      setError('Password must be at least 6 characters long');
      return;
    }

    if (newPassword !== confirmPassword) {
      setError('Passwords do not match');
      return;
    }

    setLoading(true);
    setError('');

    try {
      const response = await axios.post(`${API_BASE_URL}/api/auth/reset-password`, {
        token,
        newPassword
      });

      if (response.data.success) {
        setSuccess(true);
        setTimeout(() => {
          navigate('/login');
        }, 3000);
      }
    } catch (err) {
      setError(err.response?.data?.message || 'Failed to reset password');
    } finally {
      setLoading(false);
    }
  };

  if (validating) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-red-50 to-orange-50 flex items-center justify-center">
        <CircularProgress size={60} className="text-red-600" />
      </div>
    );
  }

  if (success) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-red-50 to-orange-50 flex items-center justify-center p-4">
        <Container maxWidth="sm">
          <Paper elevation={10} className="p-8 rounded-lg">
            <Box textAlign="center">
              <CheckCircle sx={{ fontSize: 80, color: '#2e7d32', mb: 2 }} />
              <Typography variant="h4" className="font-bold mb-3">
                Password Reset Successful!
              </Typography>
              <Typography variant="body1" color="textSecondary" className="mb-4">
                Your password has been successfully reset.
              </Typography>
              <Typography variant="body2" color="textSecondary">
                Redirecting to login page...
              </Typography>
            </Box>
          </Paper>
        </Container>
      </div>
    );
  }

  if (!tokenValid) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-red-50 to-orange-50 flex items-center justify-center p-4">
        <Container maxWidth="sm">
          <Paper elevation={10} className="p-8 rounded-lg">
            <Box textAlign="center">
              <Typography variant="h3" className="mb-4">âš ï¸</Typography>
              <Typography variant="h5" className="font-bold mb-3">
                Invalid Reset Link
              </Typography>
              <Typography variant="body1" color="textSecondary" className="mb-4">
                {error || 'This password reset link is invalid or has expired.'}
              </Typography>
              <Button
                variant="contained"
                onClick={() => navigate('/forgot-password')}
                className="bg-red-600 hover:bg-red-700"
              >
                Request New Link
              </Button>
            </Box>
          </Paper>
        </Container>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-red-50 to-orange-50 flex items-center justify-center p-4">
      <Container maxWidth="sm">
        <Paper elevation={10} className="p-8 rounded-lg">
          <Box mb={4} textAlign="center">
            <Typography variant="h3" className="font-bold text-red-600 mb-2">
              ğŸ” Reset Password
            </Typography>
            <Typography variant="body1" color="textSecondary">
              Enter your new password below
            </Typography>
          </Box>

          {error && (
            <Alert severity="error" className="mb-4">
              {error}
            </Alert>
          )}

          <form onSubmit={handleSubmit}>
            <TextField
              fullWidth
              label="New Password"
              type={showPassword ? 'text' : 'password'}
              value={newPassword}
              onChange={(e) => setNewPassword(e.target.value)}
              margin="normal"
              required
              disabled={loading}
              InputProps={{
                endAdornment: (
                  <InputAdornment position="end">
                    <IconButton onClick={() => setShowPassword(!showPassword)}>
                      {showPassword ? <VisibilityOff /> : <Visibility />}
                    </IconButton>
                  </InputAdornment>
                ),
              }}
            />

            <TextField
              fullWidth
              label="Confirm Password"
              type={showConfirmPassword ? 'text' : 'password'}
              value={confirmPassword}
              onChange={(e) => setConfirmPassword(e.target.value)}
              margin="normal"
              required
              disabled={loading}
              InputProps={{
                endAdornment: (
                  <InputAdornment position="end">
                    <IconButton onClick={() => setShowConfirmPassword(!showConfirmPassword)}>
                      {showConfirmPassword ? <VisibilityOff /> : <Visibility />}
                    </IconButton>
                  </InputAdornment>
                ),
              }}
            />

            <Typography variant="caption" color="textSecondary" className="mt-2 block">
              Password must be at least 6 characters long
            </Typography>

            <Button
              type="submit"
              fullWidth
              variant="contained"
              size="large"
              disabled={loading}
              className="mt-6 bg-red-600 hover:bg-red-700"
            >
              {loading ? <CircularProgress size={24} /> : 'Reset Password'}
            </Button>
          </form>
        </Paper>
      </Container>
    </div>
  );
};

export default ResetPassword;